import React, { useState } from "react";
import classnames from "classnames";

function Form() {
  const [req, setReq] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [company, setCompany] = useState("");
  const [phone, setPhone] = useState("");
  const [who, setWho] = useState("");

  const [reqErr, setReqErr] = useState("");
  const [nameErr, setNameErr] = useState("");
  const [emailErr, setEmailErr] = useState("");
  const [companyErr, setCompanyErr] = useState("");
  const [phoneErr, setPhoneErr] = useState("");
  const [whoErr, setWhoErr] = useState("");

  const handleChange = (e) => {
    if (e.target.name === "req") {
      setReq(e.target.req);
      setReqErr("");
    } else if (e.target.name === "name") {
      setName(e.target.req);
      setNameErr("");
    } else if (e.target.name === "email") {
      setEmail(e.target.req);
      setEmailErr("");
    } else if (e.target.name === "company") {
      setCompany(e.target.req);
      setCompanyErr("");
    } else if (e.target.name === "phone") {
      setPhone(e.target.req);
      setPhoneErr("");
    } else if (e.target.name === "who") {
      setWho(e.target.req);
      setWhoErr("");
    }
  };

  const isValidate = () => {
    let reqErr = "";
    let nameErr = "";
    let emailErr = "";
    let companyErr = "";
    let phoneErr = "";
    let whoErr = "";

    if (!req) {
      reqErr = "Your requirement is required";
    }
    if (!name) {
      nameErr = "Your name is required";
    }
    if (!email) {
      emailErr = "Email is required";
    }
    if (!company) {
      companyErr = "Company name is required";
    }
    if (!phone) {
      phoneErr = "Contact number is required";
    }
    if (!who) {
      whoErr = "Who are you?";
    }

    if (reqErr || nameErr || emailErr || companyErr || phoneErr || whoErr) {
      setReqErr(reqErr);
      setNameErr(nameErr);
      setEmailErr(emailErr);
      setCompanyErr(companyErr);
      setPhoneErr(phoneErr);
      setWhoErr(whoErr);

      return false;
    }

    return true;
  };

  const handleForm = (e) => {
    e.preventDefault();
    if (isValidate()) {
      console.log("hi");
    }
  };

  return (
    <div className="enquery-div">
      <p className="heading">Tell us what you need</p>
      <form>
        <div class="form-row">
          <div class="form-group col">
            <label for="req">Requirements</label>
            <input
              type="text"
              className={classnames("form-control", {
                "is-invalid": reqErr,
              })}
              id="req"
              name="req"
              value={req}
              onChange={handleChange}
              placeholder="Enter product/service name"
            />
            <div className="invalid-feedback">{reqErr}</div>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col">
            <label for="name">Name</label>
            <input
              type="text"
              className={classnames("form-control", {
                "is-invalid": nameErr,
              })}
              id="name"
              name="name"
              value={name}
              onChange={handleChange}
              placeholder="Full Name"
            />
            <div className="invalid-feedback">{nameErr}</div>
          </div>
          <div class="form-group col">
            <label for="email">Email</label>
            <input
              type="email"
              className={classnames("form-control", {
                "is-invalid": emailErr,
              })}
              id="email"
              name="email"
              value={email}
              onChange={handleChange}
              placeholder="name@company.com"
            />
            <div className="invalid-feedback">{emailErr}</div>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col">
            <label for="company">Company</label>
            <input
              type="text"
              className={classnames("form-control", {
                "is-invalid": companyErr,
              })}
              id="company"
              name="company"
              value={company}
              onChange={handleChange}
              placeholder="Company Name"
            />
            <div className="invalid-feedback">{companyErr}</div>
          </div>
          <div class="form-group col">
            <label for="email">Contact Number</label>
            <input
              type="text"
              className={classnames("form-control", {
                "is-invalid": phoneErr,
              })}
              id="email"
              placeholder="+94 7XX XXX XXX"
            />
            <div className="invalid-feedback">{phoneErr}</div>
          </div>
        </div>

        <div class="form-row">
          <div class="form-group col">
            <label for="who">I am a</label>
            <select
              id="who"
              className={classnames("form-control", {
                "is-invalid": whoErr,
              })}
              name="who"
              value={who}
              onChange={handleChange}
            >
              <option selected>Choose...</option>
              <option>Seller</option>
              <option>Buyer</option>
            </select>
            <div className="invalid-feedback">{whoErr}</div>
          </div>
        </div>
        <button type="submit" class="btn submit-btn" onClick={handleForm}>
          Submit
        </button>
        <p className="note">
          * To achieve our mission we provide all the necessary functionalities
          to buyers and sellers that help them in developing the voice of their
          business and to expand worldwide.
        </p>
      </form>
    </div>
  );
}

export default Form;
